#card {
    width: 20rem;
    border: 1px solid;
    box-shadow: rgb(131 131 131) 3px 12px 14px 0px;
    height: 25 rem;
    width: 20 rem;
}

#header {
    height: 5rem;
    padding: 1rem;
    box-shadow: rgb(174, 206, 210) 1px -1px 20px 6px inset;
    border-bottom: 1px solid black;
}

#body {
    box-shadow: rgb(210, 174, 174) 1px -1px 20px 6px inset;
}

.darkcard {
    width: 20rem;
    border: 1px solid white;
    box-shadow: rgb(131 131 131) 3px 12px 14px 0px;
    height: 25rem;
    width: 20rem;
    background-color: black;
    color: wheat;
}

.darkheader {
    height: 5rem;
    padding: 1rem;
    border: 1px solid white;
    /* box-shadow: rgb(174, 206, 210) 1px -1px 20px 6px inset; */
    border-bottom: 1px solid black;
}

.darkbody {
    border: 1px solid white;
    /* box-shadow: rgb(210, 174, 174) 1px -1px 20px 6px inset; */
}

/* New css */


.fl_left {
    float: left;
}

.fl_right {
    float: right;
}

img {
    margin: 0;
    padding: 0;
    border: none;
    line-height: normal;
    vertical-align: middle;
}

.imgholder, .imgl, .imgr {
    padding: 4px;
    border: 1px solid #D6D6D6;
    text-align: center;
}

.imgl {
    float: left;
    margin: 0 15px 15px 0;
    clear: left;
}

.imgr {
    float: right;
    margin: 0 0 15px 15px;
    clear: right;
}

/*----------------------------------------------HTML 5 Overrides-------------------------------------*/

address, article, aside, figcaption, figure, footer, header, nav, section {
    display: block;
    margin: 0;
    padding: 0;
}

q {
    display: block;
    padding: 0 10px 8px 10px;
    color: #979797;
    background-color: #ECECEC;
    font-style: italic;
    line-height: normal;
}

q:before {
    content: '� ';
    font-size: 26px;
}

q:after {
    content: ' �';
    font-size: 26px;
    line-height: 0;
}

/* ----------------------------------------------Wrapper-------------------------------------*/

div.wrapper {
    display: block;
    width: 100%;
    margin: 0;
    padding: 0;
    text-align: left;
}

.row1, .row1 a {
    color: #C0BAB6;
    background-color: #333333;
}

.row2 {
    color: #979797;
    background-color: #FFFFFF;
}

.row2 a {
    color: #FF9900;
    background-color: #FFFFFF;
}

.row3, .row3 a {
    color: #919191;
    background-color: #232323;
}

/*----------------------------------------------Generalise-------------------------------------*/

#header, #container, #footer {
    display: block;
    width: 960px;
    margin: 0 auto;
}

h1, h2, h3, h4, h5, h6 {
    margin: 0;
    padding: 0;
    font-size: 16px;
    font-weight: bold;
    font-style: normal;
    line-height: normal;
    text-transform: uppercase;
}


/*----------------------------------------------Content Area-------------------------------------*/

#container {
    padding: 30px 0;
}

#container section {
    display: block;
    width: 100%;
    margin: 0 0 50px 0;
    padding: 0;
}

#container .last {
    margin: 0;
}

#container .more {
    text-align: right;
}

/* ------Slider-----*/

#container #slider {}

#container #slider figure {}

#container #slider figure img {
    float: right;
    width: 630px;
    height: 300px;
}

#container #slider figure figcaption {
    display: block;
    float: left;
    width: 280px;
    height: 260px;
    padding: 20px;
    overflow: hidden;
    color: black;
    background-color: #DEDEDE;
    line-height: 1.6em;
}

#container #slider figure figcaption a {
    color: #FF9900;
    background-color: #DEDEDE;
}

#container #slider figure h2 {
    font-size: 42px;
    font-weight: normal;
    font-style: italic;
    text-transform: none;
}

#container #slider figure footer {}

