.well {
    min-height: 20px;
    padding: 19px;
    margin-bottom: 20px;
    background-color: #f5f5f5;
    border: 1px solid #e3e3e3;
    border-radius: 1px;
    box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.05);
    text-align: center;
    margin: auto !important;
    max-width: 55%;
}

.warning-container {
    width: 100vw;
    margin: -1rem;
    padding: 1.2rem;
}

.warning-text-fontsize {
    font-size: 1.1rem;
}

.mt-2 {
    margin-top: 2rem;
}

.mr-10 {
    margin-right: 5px;
}

.main-widget-btn-primary {
    background-color: #0088ce;
    background-repeat: repeat-x;
    border-color: #00659c;
    color: #fff;
    box-shadow: 0 2px 3px rgb(3 3 3 / 10%);
    border: none;
    cursor: pointer;
}